.navigation {
    background-color: $color-grey-light-2;
    display: flex;
    justify-content: space-between;
    align-items: center;
    
    a {
        transition: all .3s;
    }
    &__list {
        display: flex;
    }

    &__item {
        font-size: 2.5rem;
        font-weight: bold;
        margin-right: 2.5rem;
        padding: 1rem 2rem;
        transition: all .2s;


        &--active {
            color: red;
            transition: all 2s;
        }
    }

    &__settings {
        display: flex;
        padding: 1.5rem 3rem;
    }
}